import React from 'react'
import { Drafts, SendOutlined } from '@mui/icons-material';
import {
  Box,
  Button,
  FormControl,
  FormHelperText,
  IconButton,
  InputAdornment,
  InputLabel,
  OutlinedInput,
  Stack,
  Typography,
  useTheme
} from '@mui/material';

import Dropzone from 'react-dropzone'
// import './MailCompose.css'
// project
import useScriptRef from 'hooks/useScriptRef';
// assets
// import { images } from '__mocks__/images'

import CloudUploadIcon from '@mui/icons-material/CloudUpload';

// third party
import * as Yup from 'yup';
import { Formik } from 'formik';

import WysiwygEditor from 'components/editor/WysiwygEditor';
import MainCard from 'components/cards/MainCard';
import { useNavigate } from 'react-router-dom';
import PageContainer from 'layouts/PageContainer';
import PageHeader from 'layouts/PageHeader';

export default function BlogCreate() {
  const navigate = useNavigate();
  const theme = useTheme();
  const scriptedRef = useScriptRef();


  return (
    <PageContainer >
      <PageHeader title="New Post" back />
      <Box sx={{ display: 'flex' }}>
        <Formik
          initialValues={{
            title: ' ',
            description: ' ',

            submit: null
          }}
          validationSchema={Yup.object().shape({
            to: Yup.string().email('Must be a valid email').max(255).required('Email is required'),
            description: Yup.string().max(255).required('Subject is required')
          })}
          onSubmit={async (values, { setErrors, setStatus, setSubmitting }) => {
            try {
              if (scriptedRef.current) {
                setStatus({ success: true });
                setSubmitting(false);
              }
            } catch (err) {
              console.error(err);
              if (scriptedRef.current) {
                setStatus({ success: false });
                setErrors({ submit: err.message });
                setSubmitting(false);
              }
            }
          }}
        >
          {({ errors, handleBlur, handleChange, handleSubmit, isSubmitting, touched, values }) => (
            <form noValidate onSubmit={handleSubmit} >
              <Box
                sx={{
                  // width: 1000,
                  display: "grid",
                  gridAutoFlow: "row",
                  gridTemplateColumns: "repeat(5, 1fr)",
                  gridTemplateRows: "100px 200px 1fr 80px",
                  gap: 2,
                  flexGrow: 1
                }}
              >
                <Box sx={{ gridColumn: "1/4", gridRow: "1 / 2" }}>
                  <Stack spacing={3}>
                    <FormControl fullWidth error={Boolean(touched.title && errors.title)} sx={{ ...theme.typography.customInput }}>
                      <InputLabel htmlFor="outlined-adornment-title-blog">Post Title</InputLabel>
                      <OutlinedInput
                        id="outlined-adornment-title-blog"
                        type="title"
                        value={values.title}
                        name="title"
                        onBlur={handleBlur}
                        onChange={handleChange}
                        label="Post Title"
                        inputProps={{}}
                      />
                      {touched.title && errors.title && (
                        <FormHelperText error id="standard-weight-helper-text-title-blog">
                          {errors.title}
                        </FormHelperText>
                      )}
                    </FormControl>

                    <FormControl
                      fullWidth
                      error={Boolean(touched.description && errors.description)}
                      sx={{ ...theme.typography.customInput }}
                    >
                      <InputLabel htmlFor="outlined-adornment-description-blog">Post Description</InputLabel>

                      <OutlinedInput
                        id="outlined-adornment-description-blog"
                        type='text'
                        value={values.description}
                        name="description"
                        onBlur={handleBlur}
                        onChange={handleChange}
                        endAdornment={
                          <InputAdornment position="end">
                            <IconButton
                              aria-label="toggle description visibility"
                              // onClick={handleClickShowSubject}
                              // onMouseDown={handleMouseDownSubject}
                              edge="end"
                              size="large"
                            >
                              {/* {showSubject ? <Visibility /> : <VisibilityOff />} */}
                            </IconButton>
                          </InputAdornment>
                        }
                        label="Post Description"
                        inputProps={{}}
                      />
                      {touched.description && errors.description && (
                        <FormHelperText error id="standard-weight-helper-text-description-blog">
                          {errors.description}
                        </FormHelperText>
                      )}
                    </FormControl>
                  </Stack>
                </Box>
                <Box sx={{ gridColumn: "1/4", gridRow: "2 / 3" }}>
                  <MainCard sx={{ my: 2 }}>
                    <Typography variant='h4'>Cover</Typography>
                    <Box
                      sx={{
                        height: 80,
                        // border: 1,
                        borderRadius: `${theme.shape.borderRadius}px`,
                        bgolor: theme.palette.grey[200],
                        p: 1,
                        display: 'flex',
                        flexDirection: 'column',
                        alignItems: 'center',
                        justifyContent: 'center',
                        rowGap: 5
                      }}
                    >
                      {/* <CustomizedImage url={''} sx={{ height: 120 }} /> */}

                      <Dropzone onDrop={acceptedFiles => console.log(acceptedFiles)}>
                        {({ getRootProps, getInputProps }) => (
                          <section >
                            <div {...getRootProps()}>
                              <input {...getInputProps()} />
                              <Stack direction={`row`} alignItems={`center`} spacing={2}>
                                <IconButton color="secondary" aria-label="add an alarm" sx={{ bgcolor: theme.palette.secondary.light }}>
                                  <CloudUploadIcon />
                                </IconButton>
                                <Typography variant='h4'> Drag and drop or Click to upload</Typography>
                                {/* <Typography variant='h5'> extension: PNG,JPG,SVG,GIF / size: maximun 800X400
                                </Typography> */}
                              </Stack>
                            </div>
                          </section>
                        )}
                      </Dropzone>
                    </Box>
                  </MainCard>
                </Box>
                <Box sx={{ gridColumn: "1/4", gridRow: "3 / 4" }}>

                  <Typography variant='h4'>Content</Typography>
                  <WysiwygEditor initialContent="Write something here" />


                </Box>
                <Box sx={{ gridColumn: "4/6", gridRow: "1 / 4" }}>
                  <MainCard >
                    <Stack spacing={3}>
                      <Typography variant='h4'>Seo</Typography>
                      <FormControl fullWidth error={Boolean(touched.tags && errors.tags)} sx={{ ...theme.typography.customInput }}>
                        <InputLabel htmlFor="outlined-adornment-tags-blog"> Tags</InputLabel>
                        <OutlinedInput
                          id="outlined-adornment-tags-blog"
                          type="tags"
                          value={values.tags}
                          name="tags"
                          onBlur={handleBlur}
                          onChange={handleChange}
                          label="Tags"
                          inputProps={{}}
                        />
                        {touched.tags && errors.tags && (
                          <FormHelperText error id="standard-weight-helper-text-tags-blog">
                            {errors.tags}
                          </FormHelperText>
                        )}
                      </FormControl>
                      <FormControl fullWidth error={Boolean(touched.seotitle && errors.seotitle)} sx={{ ...theme.typography.customInput }}>
                        <InputLabel htmlFor="outlined-adornment-seotitle-blog"> SEO Title</InputLabel>
                        <OutlinedInput
                          id="outlined-adornment-seotitle-blog"
                          type="seotitle"
                          value={values.seotitle}
                          name="seotitle"
                          onBlur={handleBlur}
                          onChange={handleChange}
                          label="seo title"
                          inputProps={{}}
                        />
                        {touched.seotitle && errors.seotitle && (
                          <FormHelperText error id="standard-weight-helper-text-seotitle-blog">
                            {errors.seotitle}
                          </FormHelperText>
                        )}
                      </FormControl>
                      <FormControl fullWidth error={Boolean(touched.description && errors.description)} sx={{ ...theme.typography.customInput }}>
                        <InputLabel htmlFor="outlined-adornment-seodescription-blog" > SEO Description</InputLabel>
                        <OutlinedInput
                          id="outlined-adornment-seodescription-blog"
                          type="seodescription"
                          value={values.seodescription}
                          name="seodescription"
                          onBlur={handleBlur}
                          onChange={handleChange}
                          label="seo description"
                          inputProps={{ shrink: true }}
                          InputLabelProps={{ shrink: true }}
                        />
                        {touched.description && errors.description && (
                          <FormHelperText error id="standard-weight-helper-text-seodescription-blog">
                            {errors.description}
                          </FormHelperText>
                        )}
                      </FormControl>
                    </Stack>
                  </MainCard>
                </Box>
                <Box sx={{ gridColumn: "1/6", gridRow: "4 / 5" }}>
                  <Box sx={{ mt: 2 }}>
                    <Stack direction="row" spacing={2} justifyContent='flex-end'>
                      <Button variant="contained" endIcon={<Drafts />}>
                        Draft
                      </Button>
                      <Button
                        variant="contained"
                        endIcon={<SendOutlined />}
                        onClick={() => navigate('/pages/blogs', { replace: true })}
                      >
                        Publish
                      </Button>
                    </Stack>
                  </Box>
                </Box>

              </Box>
            </form>
          )}
        </Formik>
      </Box>
    </PageContainer>
  )
}
